"use client";

import { Table, Button, Modal, Form, Input, Space } from "antd";
import { useState } from "react";

interface User {
  id?: number;
  name: string;
  type: string;
  code: string;
}

export default function Home() {
  const [form] = Form.useForm();
  const [data, setData] = useState<User[]>([
    { id: 1, name: "John Doe", type: "admin", code: "A001" },
    { id: 2, name: "Jane Smith", type: "user", code: "U002" },
  ]);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = (record?: User) => {
    setEditingUser(record || null);
    form.setFieldsValue(record || { name: "", type: "", code: "" });
    setIsModalOpen(true);
  };

  const handleOk = () => {
    form.validateFields().then((values) => {
      if (editingUser) {
        setData((prev) =>
          prev.map((item) =>
            item.id === editingUser.id ? { ...item, ...values } : item
          )
        );
      } else {
        const newUser: User = {
          id: Date.now(), // ใช้ timestamp แทน id ชั่วคราว
          ...values,
        };
        setData((prev) => [...prev, newUser]);
      }
      setIsModalOpen(false);
      form.resetFields();
    });
  };

  const handleDelete = (id?: number) => {
    if (!id) return;
    setData((prev) => prev.filter((item) => item.id !== id));
  };

  const columns = [
    {
      title: "ชื่อ",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "ประเภท",
      dataIndex: "type",
      key: "type",
    },
    {
      title: "รหัส",
      dataIndex: "code",
      key: "code",
    },
    {
      title: "การจัดการ",
      key: "actions",
      render: (_: any, record: User) => (
        <Space>
          <Button onClick={() => showModal(record)}>แก้ไข</Button>
          <Button danger onClick={() => handleDelete(record.id)}>ลบ</Button>
        </Space>
      ),
    },
  ];

  return (
    <div style={{ padding: 24 }}>
      <h1>จัดการผู้ใช้</h1>
      <Button
        type="primary"
        onClick={() => showModal()}
        style={{ marginBottom: 16 }}
      >
        เพิ่มผู้ใช้
      </Button>
      <Table
        rowKey="id"
        dataSource={data}
        columns={columns}
      />

      <Modal
        title={editingUser ? "แก้ไขผู้ใช้" : "เพิ่มผู้ใช้"}
        open={isModalOpen}
        onOk={handleOk}
        onCancel={() => {
          setIsModalOpen(false);
          form.resetFields();
        }}
      >
        <Form form={form} layout="vertical">
          <Form.Item name="name" label="ชื่อ" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
          <Form.Item name="type" label="ประเภท" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
          <Form.Item name="code" label="รหัส" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}
